  height: 300px;
  color: white;
  text-align: center;
  background-image:url('http://en.fonditos.com/includes/imagen.php?ruta=/wallpapers/1024x768/09295.jpg&nombre=bosque_azul-1024x768.jpg');
  background-size: cover;
  background-repeat: no-repeat;
}
#Cont h1{
